  --primary-dark: #001233;
  --primary-light: #5e7ce2;
  --primary-lighter: #a1c6ea;
  --background: #121212;
  --card-bg: #1e1e1e;
  --text-primary: #e3e3e3;
  --text-secondary: #a8a8a8;

  --success: #34d399;
  --warning: #fbbf24;
  --error: #f87171;
  --neutral: #a1a1aa;
}

body {
  font-family: var(--font-roboto), sans-serif;
}
