.odd\:border-t-vsSky:nth-child(odd) {
  --tw-border-opacity: 1;
  border-top-color: rgb(195 235 250 / var(--tw-border-opacity, 1));
}
.odd\:bg-vsPurple:nth-child(odd) {
  --tw-bg-opacity: 1;
  background-color: rgb(207 206 255 / var(--tw-bg-opacity, 1));
}
.even\:border-t-vsPurple:nth-child(even) {
  --tw-border-opacity: 1;
  border-top-color: rgb(207 206 255 / var(--tw-border-opacity, 1));
}
.even\:bg-purple-300:nth-child(even) {
  --tw-bg-opacity: 1;
  background-color: rgb(216 180 254 / var(--tw-bg-opacity, 1));
}
.even\:bg-slate-50:nth-child(even) {
  --tw-bg-opacity: 1;
  background-color: rgb(248 250 252 / var(--tw-bg-opacity, 1));
}
.hover\:bg-primary-lighter:hover {
  background-color: var(--primary-lighter);
}
.hover\:bg-vsSkyLight:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(237 249 253 / var(--tw-bg-opacity, 1));
}
.disabled\:cursor-not-allowed:disabled {
  cursor: not-allowed;
}
.disabled\:opacity-50:disabled {
  opacity: 0.5;
}
